@use 'variables' as v;

.oscuro{
    background-image: url(/build/img/flat-mountains.svg);
    background-attachment: fixed;
    background-size: cover;

    .borde{
        width: 25rem;
        margin: 0 auto;
        margin-bottom: 5rem;
        border-top: 0.5rem solid v.$naranja;
        border-bottom: 0.5rem solid v.$naranja;
    }
    .modo-dark{
        filter: invert(40%);
    }
    p, h2, h5, small{
        color: v.$claro;
    }

    .perfil{
        .perfil__habilidades,
        .perfil__sobremi{
        h3,p{
            color: v.$claro;
        }    
        box-shadow: 3px 1px 15px lighten($color: v.$azul, $amount: 10%);
        }
    }

    .portafolio__trabajos{
        box-shadow: 3px 1px 15px lighten($color: v.$azul, $amount: 10%)
            
    }

    .contacto{
        .contacto__icono{
            .icon{
                filter: invert(10%);
            }
            a{
                color: lighten($color: v.$naranja, $amount: 40%);
            }
            svg{
                filter: invert(100%);
            }
            box-shadow: 3px 1px 15px lighten($color: v.$azul, $amount: 10%)
        }
    }

    .formulario{
        background-color: rgba($color: #000000, $alpha: 0.4);
        .formulario__campo,
        .formulario__campo--textarea{
            background-color: transparent;  
            color: v.$claro;  
            &::placeholder{
                color: v.$claro;
            }
        }
        
    }
}