@use 'variables' as v;
@use 'mixins' as m;

html{
    box-sizing: border-box;
    font-size: 62.5%;
}

*,*:before,*:after{
    box-sizing: inherit;
}

body{
    background-color: #ffffff;
    background-image: url(/build/img/subtle-prism.svg);
    background-attachment: fixed;
    background-size: cover;
    font-size: 1.6rem;
    font-family: v.$tipografia;
    line-height: 2.2rem;

    &.body-esp{
        @include m.tablet{
            padding-top: 70px;
        }
    }
}

h1,h2,h3{
    text-align: center;
}

h1{
    font-size: 3.8rem;
}
h2{
    font-size: 3.2rem;
}
h3{
    font-size: 2.8rem;
}

p{
    font-size: 2.2rem;
}

a{
    border: none;
    text-decoration: none;
    padding: 0;
    margin: 0;
    &:hover{
        cursor: pointer;
    }
}

img{
    display: block;
    max-width: 100%;
}

.container{
    max-width: 120rem;
    width: 95%;
    margin: 0 auto;
}

.borde{
    width: 25rem;
    margin: 0 auto;
    margin-bottom: 5rem;
    border-top: 0.2rem solid v.$naranja;
    border-bottom: 0.2rem solid v.$naranja;
}
