@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    &.nav-fijo{
        @include m.tablet{
            position: fixed;
            top: 0;
            right: 0;
            left: 0;
        }
    }
    background-color:v.$gris ;
    h1{
        color:lighten($color: v.$azul, $amount: 20%);
        margin-bottom: 3rem;
        @include m.tablet{
            margin: 0;
        }
        
    }
    .barra{
        padding: 2rem 0;
        display: flex;
        flex-direction: column;
       @include m.tablet{
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
       }
    }
    
}

.navegacion{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
    @include m.tablet{
        flex-direction: row;
        font-size: 2.2rem;
    }  
    a{
        visibility: hidden;
        display:none;
        opacity: 0;
        font-weight: 700;
        color:lighten($color: v.$azul, $amount: 20%);  
        transition: color .4s linear;
        @include m.tablet{
        visibility: visible;
        display: flex;
        opacity: 1;
        }
        &:hover{
            color:v.$claro
        }
    }
}

.barras,
.modo-dark:hover{
    cursor: pointer;
}
.barras{
    width: 3rem;
    @include m.tablet{
        display: none;
    }
}
.ocultar a{
    display: flex;
    visibility: visible;
    opacity: 1;
    @include m.tablet{
        visibility: visible;
        opacity: 1;
        display: flex;
    }
}

.modo-dark{
    width: 3rem;
    filter: invert(100%);
}
