@use 'base/variables' as v;
@use 'base/mixins' as m;

.perfil{
    padding: 5rem 0;

    h3{
        text-transform: uppercase;
        color: v.$azul;
        margin: 4rem 0;
        padding: 0;
    }

    .perfil__bloques{
        @include m.desktop{
            @include m.grid(2, 2rem);
        }
    }

    .perfil__habilidades,
    .perfil__sobremi{
        box-shadow: -5px 20px 20px v.$gris;
        background-color: rgba($color: v.$azul, $alpha: 0.2);
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 2rem;
    }


    .perfil__sobremi{
        p{
            text-align: center;
            padding: 1rem;
            line-height: 3.4rem;
        }
        a{
            
            @include m.boton(v.$naranja);
        }
    }

    
}

.perfil__habilidades{
    .habilidades__iconos{
        @include m.grid(3, 4rem);
                @include m.tablet{
                @include m.grid(4, 2rem);
            }
    }
    .habilidades__icono{
        @include m.flexcol;
        svg{
            width: 4rem;
            height: 4rem;
        }
        p{
            text-align: center;
            color: v.$azul;
            font-weight: 700;
        }
    }
}