@use 'base/variables' as v;
@use 'base/mixins' as m;

.contacto{
    padding-top: 5rem;
        h2{
            padding: 2rem 0;
        }
    .contacto__iconos{
        h5,small{
            font-size: 1.8rem;
            letter-spacing: 0.1rem;
        }
        @include m.tablet{
            @include m.grid(2, 4rem);
            padding-bottom: 0;
        }
        @include m.desktop{
            @include m.grid(4, 4rem);
        }
    }
    .contacto__icono{
        border-radius: 1rem;
        margin-bottom: 2rem;
        @include m.flexcol;
        box-shadow: -5px 10px 20px v.$gris;
        width: 100%;
        a{
            color: v.$azul;
            font-weight: 700;
        }
        svg{
            margin: 0 0.5rem;
            width: 5rem;
            height: 5rem;
        }
    }
}

.formulario{
    background-color: rgba($color: v.$claro, $alpha: 0.4);
    margin: 4rem 0;
    padding: 2rem;
    border-radius: 1rem;
    .formulario__campo,
    .formulario__campo--textarea{
        background-color: rgba($color: v.$claro, $alpha: 0.3);
        font-size: 1.8rem;
        font-weight: 700;
        padding: 1rem 0;
        padding-left: 0.5rem;
        margin-bottom: 1rem;
        display: block;
        width: 100%;
        border: none;
        border-bottom: 0.3rem solid v.$naranja;
    }

    .formulario__campo--textarea{
        resize: none;
        border: 0.3rem solid v.$naranja;
    }

    .enviar{
        display: flex;
        justify-content: flex-end;
        .btn--enviar{
            width: 100%;
        }
        @include m.tablet{
            .btn--enviar{
                width: auto;
            }
        }
    }
}