@use 'base/variables' as v;
@use 'base/mixins' as m;

.hero{
    position: relative;
    background-image: url(/build/img/logo-porfafolio.avif);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-position: center center;
    background-position: fixed;
    min-height: 40rem;
    @include m.tablet{
        min-height: 60rem;
    }
    .contenido__hero{
        position: absolute;
        width: 100%;
        height: 100%;
        @include m.flexcol;
        h2{
            font-size: 4.8rem;
            line-height: 3.8rem;
            margin: 0;
            color: v.$claro;
        }
    }
}

.btn{
    @include m.boton(v.$naranja);
    width: 100%;
    width: 95%;
    text-align: center;
    a{
        color:#fff;
    }
    @include m.tablet{
        width: auto;
    }
}