@use 'base/variables' as v;
@use 'base/mixins' as m;

.portafolio{
    padding: 5rem 0;
    margin-bottom: 5rem;
    background-color: rgba($color: #000000, $alpha: 0.4)
}

.portafolio__trabajos{
    @include m.tablet{
        @include m.grid(2,2rem);
    }
    .portafolio__trabajo{
        margin-bottom: 3rem;
        @include m.tablet{
            margin-bottom: 0;
        }
        box-shadow: -5px 20px 20px v.$gris;
        img{
            width: 100%;
            height: 40rem;
        }
    }
}

